<template>
  <v-app id="inspire">
    <div id="line-space"></div>
    <v-layout row>
      <v-flex xs12 sm6 offset-sm3>
        <v-card>
          <v-img
              src="https://cdn.arstechnica.net/wp-content/uploads/2017/10/getty-security.jpg"
              aspect-ratio="2.75"
          ></v-img>

          <v-card-title primary-title>
            <div>
              <h3 class="headline mb-0">{{ encryptionTitle }}</h3>
              <div v-for="i in encryptiontDescription" :key="i">{{ i }}</div>
            </div>
          </v-card-title>
        </v-card>

        <div id="line-space"></div>
        <div id="hash-wrapper">
            <div id="hash-description">
              Text  SHA-256   .
            </div>
            <v-textarea
              outline
              name="input-7-4"
              label="Input text"
              value=""
              v-model="inputText"
            ></v-textarea>
            <v-text-field
              value=""
              label="Output sha-256"
              outline
              readonly
              v-model="sha256Ret"
            ></v-text-field>
        </div>

        <div id="line-space"></div>
        <div id="hash-wrapper">
            <div id="hash-description">
              <div> 64   Key  Private key  </div>
              <div> Private Key     Public Key . </div>
              <div>  Public Key SHA-256    RIPEMD-160  </div>
              <div> 32  Public key . </div>
            </div>
            <v-btn block color="error" @click="randomGenerate()">Random Generator</v-btn>
            <v-text-field
              v-model="privateKey"
              counter="64"
              maxlength="64"
              label="Private key"
              outline
              readonly
            ></v-text-field>
            <v-text-field
              value=""
              label="Public key"
              counter="32"
              outline
              readonly
              v-model="publicKey"
            ></v-text-field>
        </div>

        <div id="line-space"></div>
        <div id="hash-wrapper">
            <div id="hash-description">
              <div> Text Private key   . </div>
              <div>    Private key  </div>
              <div>   Text    . </div>
            </div>
            <v-textarea
              outline
              name="input-7-4"
              label="Input text"
              value=""
              v-model="signInputText"
            ></v-textarea>
            <v-text-field
              value=""
              label="Private key"
              counter="64"
              maxlength="64"
              outline
              v-model="signPrivateKey"
            ></v-text-field>
            <v-btn block color="success" @click="signClick()">Sign</v-btn>
            <div id="line-space"></div>
            <v-text-field
              value=""
              label="Message Signature"
              outline
              readonly
              v-model="signKey"
            ></v-text-field>
        </div>

        <div id="line-space"></div>
        <div id="hash-wrapper">
            <div id="hash-description">
              <div>      . </div>
              <div>  Text Private key    </div>
              <div> Text, Public key       </div>
              <div>     . </div>
            </div>
            <v-textarea
              outline
              name="input-7-4"
              label="Input text"
              value=""
              v-model="verifyInputText"
            ></v-textarea>
            <v-text-field
              value=""
              label="Public key"
              counter="32"
              maxlength="32"
              outline
              v-model="verifyPublicKey"
            ></v-text-field>
            <v-text-field
              value=""
              label="Message Signature"
              outline
              v-model="verifySignKey"
            ></v-text-field>
            <v-btn block color="info" @click="verifyClick()">Verify</v-btn>
        </div>
      </v-flex>
    </v-layout>
    <!--    msg -->
    <div v-if="isValidSign" class="text-xs-center">
      <v-dialog v-model="isValidSign" width="800">
        <v-card>
          <v-card-title class="headline grey lighten-2" primary-title>
            {{ validSignTitle }}
          </v-card-title>

          <v-card-text v-for="(i, key) in validSignDescription" :key="i">
            {{ key }} : {{ i }}
          </v-card-text>

          <v-divider></v-divider>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              color="primary"
              flat
              @click="isValidSign = false"
            >
              {{ ok }}
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>

    <!--    msg -->
    <v-snackbar
      v-model="isInValidSign"
      :bottom="y === 'bottom'"
      :left="x === 'left'"
      :right="x === 'right'"
      :timeout="timeout"
      :top="y === 'top'"
      :vertical="mode === true"
      :auto-height="true"
    >
      <div id="invalid-text">Invalid Signature</div>
    </v-snackbar>
  </v-app>
</template>

<script>
import SHA256 from '../js/function.js'
import { db } from '../js/db.js'
import * as ECC from '../js/ecc.js'

export default {
  name: 'app',
  props: ['id'],
  mounted () {

  },
  data () {
    return {
      encryptionTitle: 'What is encryption in blockchain?',
      encryptiontDescription: ['           .',
        '   ,             .',
        '             .',
        '   SHA-256   .'],
      inputText: '',
      sha256Ret: '',
      privateKey: '',
      publicKey: '',
      signInputText: '',
      signPrivateKey: '',
      signPublicKey: '',
      signKey: '',
      verifyInputText: '',
      verifyPublicKey: '',
      verifySignKey: '',
      isValidSign: false,
      validSignTitle: 'This signature is valid!',
      validSignDescription: '',
      ok: 'OK',
      isInValidSign: false,

      // snackbar variable
      y: 'top',
      x: null,
      mode: '',
      timeout: 3000
    }
  },
  firebase () {
    return {
      signs: db.ref('sign')
    }
  },
  methods: {
    signClick () {
      this.signPrivateKey = this.privateKey
      this.signPublicKey = this.publicKey
      this.signKey = SHA256(this.signPrivateKey + this.signInputText)
      let ret = {signInputText: this.signInputText, privateKey: this.signPrivateKey, publicKey: this.signPublicKey, signKey: this.signKey}
      db.ref('sign').push(ret)
    },
    verifyClick () {
      for (let i = 0; i < this.signs.length; i++) {
        if (this.verifyInputText === this.signs[i].signInputText && this.verifyPublicKey === this.signs[i].publicKey && this.verifySignKey === this.signs[i].signKey) {
          this.isValidSign = true
          this.validSignDescription = {
            InputText: this.signs[i].signInputText,
            PrivateKey: this.signs[i].privateKey,
            PublicKey: this.signs[i].publicKey,
            SignKey: this.signs[i].signKey
          }
          return
        }
      }
      this.isInValidSign = true
    },
    randomGenerate () {
      let arr = '0123456789abcdef'

      this.privateKey = ''
      for (let i = 0; i < 64; i++) {
        this.privateKey += arr[parseInt(Math.random() * 100 % 16)]
      }
      this.makePublicKey()
    },
    makePublicKey () {
      let privateKey = new Buffer(this.privateKey, 'hex')

      let curvePt = ECC.ecparams.G.multiply(ECC.BigInteger.fromBuffer(privateKey))
      let x = curvePt.affineX.toBuffer(32)
      let y = curvePt.affineY.toBuffer(32)

      let publicKey = Buffer.concat([new Buffer([0x04]), x, y])

      publicKey = curvePt.getEncoded(false)

      publicKey = curvePt.getEncoded(true)
      let sha = ECC.crypto.createHash('sha256').update(publicKey).digest()
      let pubkeyHash = ECC.crypto.createHash('rmd160').update(sha).digest()

      this.publicKey = '2'
      for (let i = 0; i < 31; i++) {
        this.publicKey += pubkeyHash.toString('hex')[i]
      }
    }
  },
  watch: {
    inputText () {
      this.sha256Ret = SHA256(this.inputText)
    }
  }
}
</script>

<style scoped>
  #line-space{
    margin-top: 20px
  }
  .ml10{
    margin-left:20px
  }
  #hash-wrapper{
    width:100%;
  }
  #hash-description{
    color: #2196f3;
    margin-bottom: 15px;
    margin-top: 25px;
    text-align: center;
    font-weight: bold;
    font-size: 15px;
  }
  #invalid-text{
    text-align: center;
    color: yellow;
    width: 100%;
    font-size: 15px;
    font-weight: bold;
  }
</style>



